.fade
    width: 100%
    height: 100%
    position: absolute
    background-color: #000000
    opacity: .3

.modal
    position: absolute
    top: 100px
    left: 0
    right: 0
    margin: 0 auto
    width: 460px
    height: 400px
    z-index: 1
    padding: 20px
    border: 1px solid #cecece
    background-color: #ececec
    border-radius: 20px
    box-shadow: 5px 5px 6px #00000020
    display: flex
    flex-direction: column
    justify-content: center
    text-align: center

    h2
        margin-bottom: 1em